import Vue from 'vue'
import Router from 'vue-router'
import DashboardLayout from '@/layout/DashboardLayout'
import AuthLayout from '@/layout/AuthLayout'
import store from '@/store/store.js'
Vue.use(Router)

const routes = [
  {
    path: '/',
    mode:'history',
    base:'/dist',
    redirect: 'login',
    component: AuthLayout,
    children: [
      {
        path: '/login',
        name: 'login',
        component: () => import(/* webpackChunkName: "demo" */ './views/Login.vue')
      },
      {
        path: '/register',
        name: 'register',
        component: () => import(/* webpackChunkName: "demo" */ './views/Register.vue')
      }
    ]
  },
  {
    path: '/',
    redirect: 'dashboard',
    component: DashboardLayout,
    children: [
      {
        path: '/dashboard',
        name: 'dashboard',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "demo" */ './views/Dashboard.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/icons',
        name: 'icons',
        component: () => import(/* webpackChunkName: "demo" */ './views/Icons.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/profile',
        name: 'profile',
        component: () => import(/* webpackChunkName: "demo" */ './views/UserProfile.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/maps',
        name: 'maps',
        component: () => import(/* webpackChunkName: "demo" */ './views/Maps.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_employee',
        name: 'tables employee',
        component: () => import(/* webpackChunkName: "demo" */ './views/Tables.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_position',
        name: 'tables position',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesPosition.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_branch',
        name: 'tables branch',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesBranch.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_clinic',
        name: 'tables clinic',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesClinic.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_group',
        name: 'tables group',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesGroup.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_sub_group',
        name: 'tables sub group',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesSubGroup.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_wirehouse',
        name: 'tables wirehouse',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesWarehouse.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_item',
        name: 'tables item',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesItem.vue'),
        meta: { 
          requiresAuth: true
        }
      },
      {
        path: '/tables_unit_of_measure',
        name: 'tables unit of measure',
        component: () => import(/* webpackChunkName: "demo" */ './views/TablesUnitOfMeasure.vue'),
        meta: { 
          requiresAuth: true
        }
      },
    ]
  },
]

const router = new Router({
  linkExactActiveClass: 'active',
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    // eslint-disable-next-line no-undef
    if (store.getters.isLoggedIn) {
      next()
      return
    }
    next('/login') 
  } else {
    next() 
  }
})

export default router

